  </div>
</template>

<script>
export default {
  name: "hello",
  data() {
    return {
      msg: "Welcome to Your Vue.js PWA",
      alert: false
    };
  },
  methods: {
    vibrate() {
      window.navigator.vibrate(200);
    }
  }
};
</script>



